@import url("https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap");

/* font-family: 'Fira Sans', sans-serif; */

body {
  margin: 0;
  font-family: "Fira Sans", sans-serif;
  /*colors*/
  --color1: rgb(0, 0, 0);
  --color2: rgb(189, 249, 152);
  --color3: #f9f3a3;
  --color4: #f96b61;
  --color5: #ffffff;

  --text-focus: rgb(255, 170, 12);
  --text-secondary: white;

  background: var(--color2);
  /* animation: bgcolor infinite 10s ease-in-out; */
  color: var(--color2);
}

@keyframes bgcolor {
  0% {
    background: black;
  }
  33% {
    background: rgb(58, 44, 9);
  }
  50% {
    background: rgb(22, 0, 17);
  }
  66% {
    background: rgb(55, 8, 8);
  }
  100% {
    background: black;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}
